import pluralize from '~/utils/pluralize'

export default {
	props: {
		home: {
			type: Object,
			required: true
		}
	},
	computed: {
		features() {
			return this.home.features.slice(0, 3).join(', ')
		}
	},
	methods: {
		pluralize
	}
}
